.Placeholder {
  color : rgba(0,0,0,0) !important;
  background : none !important;
  position : relative;
}

.Placeholder * {
  visibility : hidden;
}

.Placeholder:after {
  content : " ";
  z-index : 10;
  display : block;
  position : absolute;
  height : 100%;
  top : 0;
  left : 0;
  right : 0;
  pointer-events : none;
  background : -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, .06)), color-stop(15%, #f8f8f8), color-stop(30%, rgba(0, 0, 0, .06)));
  background : -webkit-linear-gradient(left, rgba(0, 0, 0, .06) 0%, #f8f8f8 15%, rgba(0, 0, 0, .06) 30%);
  background : -o-linear-gradient(left, rgba(0, 0, 0, .06) 0%, #f8f8f8 15%, rgba(0, 0, 0, .06) 30%);
  background : linear-gradient(to right, rgba(0, 0, 0, .06) 0%, #f8f8f8 15%, rgba(0, 0, 0, .06) 30%);
  background-size : 200vw 100%;
  background-attachment: fixed;
  pointer-events : none;
  -webkit-animation : 1.3s linear infinite forwards Placeholder;
          animation : 1.3s linear infinite forwards Placeholder;
  border-radius : 5px;
}

@-webkit-keyframes Placeholder {
  0% {
    background-position: -100vw 0;
  }
  60% {
    background-position: 100vw 0;
  }
  100% {
    background-position: 100vw 0;
  }
}

@keyframes Placeholder {
  0% {
    background-position: -100vw 0;
  }
  60% {
    background-position: 100vw 0;
  }
  100% {
    background-position: 100vw 0;
  }
}



