/* TODO css grid layout */
.Repository .Contributors {
  margin : 1.5em auto 0 auto;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-align : center;
      align-items : center;
  -ms-flex-wrap : wrap;
      flex-wrap : wrap;
  max-width : calc(18*54px);
}

.Repository .Contributors a {
  display : -ms-inline-flexbox;
  display : inline-flex;
}

.Repository .Contributors .Picture {
  width : 40px;
  height : 40px;
  border-radius : 100px;
  margin : 7px;
  -webkit-animation : .3s forwards Contributors;
          animation : .3s forwards Contributors;
  opacity : 0;
}

.Repository .Contributors .Picture:hover {
  -webkit-transform : scale(1.1);
      -ms-transform : scale(1.1);
          transform : scale(1.1);
}

.Repository .Contributors .Picture:after {
  border-radius : 100px;
}



@-webkit-keyframes Contributors {
  from {
    -webkit-transform : scale(0);
            transform : scale(0);
  }
  to {
    opacity : 1;
  }
}



@keyframes Contributors {
  from {
    -webkit-transform : scale(0);
            transform : scale(0);
  }
  to {
    opacity : 1;
  }
}



