.MadeWithLove {
  margin-top : auto;
  overflow : hidden;
  width : 100%;
}

.MadeWithLove .inner {
  display : -ms-flexbox;
  display : flex;
  width : 100%;
  -ms-flex-pack : center;
      justify-content : center;
  -ms-flex-align : center;
      align-items : center;
  padding : 2.5em;
  font-size : 14px;
  -webkit-animation : .3s forwards MadeWithLove;
          animation : .3s forwards MadeWithLove;
}

.MadeWithLove .inner > * {
  margin : 0 4px;
}

.MadeWithLove .SVG {
  max-width : 20px;
}

.MadeWithLove b {
  font-weight : 700;
}



@-webkit-keyframes MadeWithLove {
  from {
    -webkit-transform : translateY(100%);
            transform : translateY(100%);
    opacity : .2;
  }
}



@keyframes MadeWithLove {
  from {
    -webkit-transform : translateY(100%);
            transform : translateY(100%);
    opacity : .2;
  }
}



